@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Helvetica"), url("../fonts/helvetica-regular.woff2") format("woff2"), url("../fonts/helvetica-regular.woff") format("woff");
}
@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Helvetica"), url("../fonts/helvetica-bold.woff2") format("woff2"), url("../fonts/helvetica-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page,
.page-body {
  margin: 0;
  padding: 0;
  height: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.scroll-lock {
  overflow: hidden;
}
.container {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 767px) {
  .container {
    max-width: 100%;
    padding: 0 16px;
  }
}
.page {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #000000;
}
.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .page-body {
    position: relative;
  }
}
.page-body.shadow::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(3, 16, 27, 0.7);
  z-index: 4;
  transition: background 0.4s ease;
}
.page-body.shadow {
  overflow: hidden;
}
.header {
  padding-top: 30px;
  margin-bottom: 50px;
}
@media (max-width: 767px) {
  .header {
    position: relative;
    padding-top: 16px;
    margin-bottom: 37px;
  }
}
@media (max-width: 767px) {
  .header.is-open.header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 60px;
    background: linear-gradient(180deg, #ffffff 0%, #1ea89b 0%, #5639ad 65%);
    z-index: 8;
  }
}
/* Грид раскладка блоков */
.header__container {
  display: grid;
  grid-template-columns: 1fr min-content 1fr;
  row-gap: 64px;
  grid-template-rows: 1fr 1fr;
}
@media (max-width: 767px) {
  .header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__user-nav {
  justify-self: end;
}
.header__logo {
  grid-row-start: 1;
  grid-row-end: -1;
  grid-column-start: 2;
  grid-column-end: 3;
  align-self: end;
}
.header__site-nav {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 2;
  grid-row-end: 3;
  align-self: end;
}
.header__site-nav--right {
  justify-self: end;
}
.user-nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 21px;
  margin-right: 6px;
}
@media (max-width: 767px) {
  .user-nav__list {
    gap: 16px;
    margin: 0 10px 0 0;
  }
}
@media (max-width: 767px) {
  .user-nav__item--profile {
    display: none;
  }
}
.user-nav__link {
  display: inline-block;
  vertical-align: middle;
  height: 27px;
  stroke: #000000;
  fill: transparent;
  transition: fill 0.2s linear, stroke 0.2s ease;
}
@media (max-width: 767px) {
  .user-nav__link {
    height: 22px;
  }
}
.user-nav__link--profile {
  width: 24px;
}
@media (min-width: 1110px) {
  .user-nav__link--profile:hover,
  .user-nav__link--profile:focus {
    stroke: #000000;
    fill: #ff8f00;
  }
  .user-nav__link--profile:active {
    fill: #fd0202;
  }
}
.user-nav__link--profile:active {
  fill: #fd0202;
}
.user-nav__link--favourites {
  width: 30px;
}
@media (max-width: 767px) {
  .user-nav__link--favourites {
    width: 24px;
  }
}
@media (min-width: 1110px) {
  .user-nav__link--favourites:hover,
  .user-nav__link--favourites:focus {
    stroke: #fd0202;
    fill: #fd0202;
  }
  .user-nav__link--favourites:active {
    fill: transparent;
  }
}
.user-nav__link--favourites:active {
  fill: #fd0202;
  transition-duration: 0s;
}
.user-nav__link--cart {
  width: 25px;
}
@media (max-width: 767px) {
  .user-nav__link--cart {
    width: 20px;
  }
}
@media (min-width: 1110px) {
  .user-nav__link--cart:hover,
  .user-nav__link--cart:focus {
    fill: #7ed7f2;
  }
  .user-nav__link--cart:active {
    fill: #00ffcf;
  }
}
.user-nav__link--cart:active {
  fill: #00ffcf;
  transition-duration: 0s;
}
.user-nav__link svg {
  width: 100%;
  height: 100%;
}
/* Корзина */
.user-nav__item--cart {
  position: relative;
}
.user-nav__item--cart span {
  position: absolute;
  top: -3px;
  right: -7px;
  font-size: 12px;
  line-height: 14px;
  width: 17px;
  height: 16px;
  text-align: center;
  background-color: #000000;
  color: #ffffff;
  border-radius: 50%;
  pointer-events: none;
}
@media (max-width: 767px) {
  .user-nav__item--cart span {
    right: -11px;
  }
}
.site-nav {
  display: flex;
  column-gap: 160px;
}
@media (max-width: 767px) {
  .site-nav {
    position: fixed;
    column-gap: 0;
    flex-direction: column;
    background: linear-gradient(0deg, #ffffff 0%, #1ea89b 0%, #5639ad 65%);
    width: 100%;
    overflow: auto;
    height: 100%;
    top: -100%;
    left: 50%;
    transform: translate(-50%);
    z-index: 5;
    transition: top 0.4s ease-in-out, padding 0.4s ease-in-out;
  }
}
@media (max-width: 767px) {
  .site-nav.is-open {
    top: 0;
    padding: 80px 0 25px;
  }
}
.site-nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  width: 50%;
  display: flex;
  flex-wrap: wrap;
  gap: 31px;
  padding-right: 10px;
}
.site-nav__list--right {
  justify-content: flex-end;
  padding-left: 10px;
  padding-right: 0;
}
@media (max-width: 767px) {
  .site-nav__list {
    width: 100%;
    padding: 0;
    flex-direction: column;
    align-items: center;
    gap: 0;
  }
}
.site-nav__item--profile {
  display: none;
}
@media (max-width: 767px) {
  .site-nav__item {
    width: 100%;
    text-align: center;
  }
  .site-nav__item--profile {
    display: block;
  }
}
.site-nav__item a {
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  color: #9f9f9f;
  text-decoration: none;
  text-transform: uppercase;
  transition: color 0.2s ease;
}
@media (max-width: 767px) {
  .site-nav__item a {
    display: block;
    padding: 10px;
    letter-spacing: 1px;
    color: #dddb6a;
  }
}
@media (min-width: 1110px) {
  .site-nav__item a:hover,
  .site-nav__item a:focus {
    color: #000000;
    background-color: transparent;
  }
  .site-nav__item a:active {
    color: #f68050;
  }
}
.site-nav__item a:active {
  color: #f68050;
}
.logo {
  display: block;
  width: 160px;
  height: 50px;
}
@media (max-width: 767px) {
  .logo {
    width: 77px;
    height: 24px;
    align-self: center;
  }
}
.header__logo svg {
  width: 100%;
  height: 100%;
}
.search-form {
  margin-top: -5px;
}
@media (max-width: 767px) {
  .search-form {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 15px;
    height: 70px;
    padding: 0 10px;
    margin-top: 0;
    position: fixed;
    top: -75px;
    left: 0;
    right: 0;
    background: linear-gradient(261deg, #ffffff 0%, #4dcdf2 0%, #3b4266 54%);
    z-index: 10;
    transition: top 0.3s ease-in-out;
  }
  .search-form.is-open {
    top: 0;
  }
}
.search-form__close {
  display: none;
}
@media (max-width: 767px) {
  .search-form__close {
    display: block;
    position: relative;
    flex-shrink: 0;
    width: 30px;
    height: 30px;
    padding: 0;
    cursor: pointer;
    border: none;
    background-color: transparent;
  }
  .search-form__close::before,
  .search-form__close::after {
    content: "";
    position: absolute;
    height: 2px;
    background-color: #aedaf7;
    left: 0;
    transform: rotate(0deg);
    width: 0;
    transition: width 0.3s ease-in-out 0.4s, transform 0.3s ease-in-out;
  }
}
.search-form.is-open .search-form__close::before {
  transform: rotate(45deg);
  width: 100%;
  transition: width 0.3s ease-in-out, transform 0.3s ease-in-out 0.4s;
}
.search-form.is-open .search-form__close::after {
  transform: rotate(-45deg);
  width: 100%;
  transition: width 0.3s ease-in-out, transform 0.3s ease-in-out 0.4s;
}
.search-form__group {
  display: flex;
  align-items: center;
  margin: 0;
}
@media (max-width: 767px) {
  .search-form__group {
    display: block;
    max-width: 100%;
  }
}
.search-form__label {
  position: relative;
  padding-left: 42px;
}
@media (max-width: 767px) {
  .search-form__label {
    padding-left: 0;
  }
}
.search-form__label::before {
  content: "";
  position: absolute;
  background-image: url("../img/search-icon.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  width: 27px;
  height: 27px;
  left: 0;
  top: 55%;
  transform: translateY(-45%);
}
@media (max-width: 767px) {
  .search-form__label::before {
    display: none;
  }
}
.search-form__group input {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  max-width: 250px;
  padding: 5px;
  border: none;
  border-bottom: 1px solid #9f9f9f;
}
.search-form__group input::placeholder {
  color: #9f9f9f;
  transition: color 0.2s linear;
}
@media (max-width: 767px) {
  .search-form__group input {
    box-shadow: 0 0 24px #7ed7f2;
    max-width: none;
    width: 100%;
    border-radius: 8px;
    border: none;
  }
}
.search-form__label:hover input::placeholder {
  color: #000000;
}
.search-form__find {
  display: none;
}
@media (max-width: 767px) {
  .search-form__find {
    height: 30px;
    padding: 0;
    cursor: pointer;
    border: 0;
    background-color: #8c59d5;
    border-radius: 8px;
    min-width: 70px;
    font-weight: 700;
    color: #ffffff;
    line-height: 19px;
    font-size: 14px;
    display: inline-block;
  }
}
.burger {
  display: none;
}
@media (max-width: 767px) {
  .burger {
    display: inline-block;
    position: relative;
    padding: 5px 0;
    height: 30px;
    width: 28px;
    vertical-align: middle;
    cursor: pointer;
    border: none;
    background-color: transparent;
    z-index: 9;
  }
  .burger::before,
  .burger::after {
    content: "";
    position: absolute;
    height: 2px;
    background-color: #000000;
    width: 100%;
  }
  .burger::before {
    top: 5px;
    right: 0;
    transition: width 0.3s ease-in-out, right 0.3s ease-in-out;
  }
  .burger::after {
    bottom: 5px;
    left: 0;
    transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
  }
}
.burger__menu {
  display: block;
  position: relative;
  width: 28px;
  height: 2px;
  background-color: #000000;
  margin: 0 auto;
  z-index: 1;
  pointer-events: none;
  transition: transform 0.3s;
}
.burger__menu::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #000000;
  left: 0;
  transition: transform 0.3s;
}
.burger.is-open::after {
  width: 0%;
  left: 0;
  transition: all 0.3s;
  background-color: #fd0202;
}
.burger.is-open::before {
  width: 0%;
  right: 0;
  transition: all 0.3s;
  background-color: #fd0202;
}
.burger.is-open .burger__menu,
.burger.is-open .burger__menu::before {
  transform: rotate(45deg);
  transition: 0.3s transform 0.3s;
  background-color: #dddb6a;
}
.burger.is-open .burger__menu::before {
  transform: rotate(-90deg);
}
.mobile-nav {
  display: none;
}
@media (max-width: 767px) {
  .mobile-nav {
    display: flex;
    align-items: center;
    column-gap: 12px;
  }
}
@media (max-width: 767px) {
  .mobile-nav__search-site {
    width: 32px;
    height: 32px;
    padding: 5px;
    cursor: pointer;
    border: none;
    background-color: transparent;
    user-select: none;
  }
}
@media (max-width: 767px) {
  .mobile-nav__search-site svg {
    width: 100%;
    height: 100%;
    fill: none;
  }
}
.mobile-nav__search-site:active svg {
  fill: #f68050;
}
.breadcrumbs {
  margin-bottom: 48px;
}
@media (max-width: 767px) {
  .breadcrumbs {
    margin-bottom: 30px;
  }
}
.breadcrumbs__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}
.breadcrumbs__item:not(:last-child)::after {
  content: "\2044";
  display: inline-block;
  position: relative;
  padding: 0 10px;
  color: #9f9f9f;
  transform: rotate(-13deg) translate(-3px, -1px);
}
.breadcrumbs__item a {
  font-size: 18px;
  line-height: 21px;
  color: #9f9f9f;
  text-decoration: none;
  transition: color 0.2s ease;
}
@media (min-width: 1200px) {
  .breadcrumbs__item a:hover,
  .breadcrumbs__item a:focus {
    color: #000000;
  }
  .breadcrumbs__item a:active {
    color: #f68050;
  }
}
@media (max-width: 767px) {
  .breadcrumbs__item a {
    font-size: 18px;
    line-height: 21px;
  }
}
.breadcrumbs__item a:active {
  color: #f68050;
}
.breadcrumbs__item.is-current a {
  color: #000000;
  cursor: default;
}
.basket {
  margin-bottom: 70px;
}
.authorization-link {
  margin-top: -3px;
}
@media (max-width: 767px) {
  .authorization-link {
    margin-bottom: 34px;
    margin-top: -12px;
  }
}
.authorization-link span {
  line-height: 19px;
  cursor: default;
}
.authorization-link a {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-decoration: underline;
  padding: 2px;
  cursor: pointer;
  color: #9f9f9f;
  background-color: transparent;
  border: none;
  transition: color 0.2s ease, text-decoration 0.2s ease-in-out;
}
@media (min-width: 1200px) {
  .authorization-link a:hover,
  .authorization-link a:focus {
    color: #000000;
    text-decoration: none;
  }
  .authorization-link a:active {
    color: #f68050;
  }
}
.authorization-link a:active {
  color: #f68050;
  text-decoration: none;
}
.basket-form {
  position: relative;
  display: grid;
  grid-template-columns: 505px 1fr;
  grid-template-rows: 46px 46px 46px 46px 46px;
  column-gap: 50px;
  row-gap: 19px;
}
@media (max-width: 767px) {
  .basket-form {
    display: flex;
    flex-direction: column;
    row-gap: 25px;
    align-items: stretch;
  }
}
.basket-form h1,
.basket__authorization,
.basket-form__group,
.basket-form__block,
.selected-products,
.basket-form button {
  grid-column: 1/2;
}
.basket-form__group {
  position: relative;
  margin: 0;
}
.basket-form__block {
  display: flex;
  column-gap: 35px;
}
@media (max-width: 767px) {
  .basket-form__block {
    flex-direction: column;
    column-gap: 0;
    margin-top: 15px;
    row-gap: 32px;
    margin-bottom: 7px;
  }
}
.basket-form__block .basket-form__group {
  width: 50%;
}
@media (max-width: 767px) {
  .basket-form__block .basket-form__group {
    width: 100%;
  }
}
.basket-form h1 {
  font-weight: 700;
  font-size: 40px;
  line-height: 46px;
  margin: 0 0 2px;
}
@media (max-width: 767px) {
  .basket-form h1 {
    font-size: 30px;
    line-height: 34px;
    margin: 0;
  }
}
.basket-form__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 35px;
  row-gap: 20px;
  margin-bottom: 70px;
}
@media (max-width: 767px) {
  .basket-form__list {
    display: flex;
    flex-direction: column;
    column-gap: 0;
    row-gap: 16px;
  }
}
.basket-form__group input,
.basket-form__group textarea {
  font-family: "Helvetica", "Arial", sans-serif;
  line-height: 19px;
  font-size: 16px;
  width: 100%;
  border: none;
  border-bottom: 1px solid #9f9f9f;
  padding: 5px;
  resize: none;
  outline: none;
}
.basket-form__group textarea {
  height: 35px;
  /* Скрываем полосу прокрутки т.к высота textarea увеличивается автоматически */
  overflow: hidden;
}
.basket-form__label {
  position: absolute;
  top: 0;
  left: 0;
  color: #9f9f9f;
  line-height: 19px;
  pointer-events: none;
  transition: all 0.3s ease-in-out;
}
.basket-form__input:focus ~ .basket-form__label,
.basket-form__input:not(:placeholder-shown) ~ .basket-form__label {
  font-size: 12px;
  opacity: 0.7;
  top: -15px;
}
.basket-form__btn-buy {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
  width: 100%;
  height: 70px;
  margin-top: 5px;
  background: transparent;
  border: 1px solid #000000;
  padding: 24px 50px;
  user-select: none;
  cursor: pointer;
  transition: color 0.3s linear, background 0.5s linear;
}
@media (max-width: 767px) {
  .basket-form__btn-buy {
    height: auto;
    margin-top: 1px;
    padding: 17px 50px;
  }
}
@media (min-width: 1200px) {
  .basket-form__btn-buy:hover,
  .basket-form__btn-buy:focus {
    color: #ffffff;
    background: linear-gradient(90deg, #f68050 0%, #0c0c0c 100%);
  }
  .basket-form__btn-buy:active {
    background: linear-gradient(90deg, #0c0c0c 0%, #f68050 100%);
    color: #000000;
  }
}
.basket-form__btn-buy:active {
  background: linear-gradient(90deg, #0c0c0c 0%, #f68050 100%);
  color: #000000;
}
.basket-form__group input:valid {
  border-bottom-color: #009956;
  border-width: 1px;
}
.basket-form__group input:valid ~ label {
  color: #009956;
}
/* По умолчанию поля пустые и они обязательны для заполнения, поэтому браузер считает, что они уже не валидны. Исправим это */
.basket-form__group input:not(:focus):invalid {
  color: #000000;
  border-bottom-color: #9f9f9f;
  border-width: 1px;
}
.basket-form__group input:not(:focus):invalid ~ label {
  color: #9f9f9f;
}
.basket-form__group input:invalid {
  color: #fd0202;
  border-bottom-color: #fd0202;
  border-width: 1px;
}
.basket-form__group input:invalid ~ label {
  color: #fd0202;
}
.selected-products {
  margin-top: 47px;
}
@media (max-width: 767px) {
  .selected-products {
    margin-top: 11px;
    margin-bottom: 12px;
  }
}
.selected-products h2 {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  margin: 0;
  margin-bottom: 5px;
}
@media (max-width: 767px) {
  .selected-products h2 {
    font-size: 26px;
    line-height: 30px;
    margin: 0 0 17px;
  }
}
.selected-products__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}
.product {
  display: grid;
  grid-template-columns: 150px 1fr min-content;
  column-gap: 20px;
  border-bottom: 1px solid #dedede;
  padding: 25px 0;
  margin-right: 35px;
}
@media (max-width: 767px) {
  .product {
    grid-template-columns: 80px 1fr min-content;
    column-gap: 13px;
    padding: 16px 0;
    margin-right: 0;
  }
}
.product:last-child {
  border-bottom: none;
  padding-bottom: 0;
}
.product__description {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 150px;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: -1;
}
@media (max-width: 767px) {
  .product__description {
    max-width: 110px;
  }
}
.product__description h3 {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  margin: 0 0 10px;
}
@media (max-width: 767px) {
  .product__description h3 {
    font-size: 16px;
    line-height: 18px;
    margin: 0;
  }
}
.product__description p {
  line-height: 19px;
  color: #9f9f9f;
  margin: 0;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .product__description p {
    display: none;
  }
}
/* product__image */
.product__image {
  width: 150px;
  height: 150px;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: -1;
}
@media (max-width: 767px) {
  .product__image {
    width: 80px;
    height: 80px;
  }
}
.product__image img {
  width: 100%;
  height: 100%;
  background-size: cover;
}
.product__price {
  grid-column-start: 3;
  grid-column-end: 4;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: end;
}
.product__price p {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  margin: 0;
}
@media (max-width: 767px) {
  .product__price p {
    font-size: 16px;
    line-height: 18px;
  }
}
.product__price span {
  margin-left: 5px;
}
.product button {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  width: auto;
  padding: 5px;
  border: none;
  color: #9f9f9f;
  line-height: 16px;
  text-decoration: underline;
  background-color: transparent;
  user-select: none;
  cursor: pointer;
  transition: text-decoration 0.3s ease;
}
@media (max-width: 767px) {
  .product button {
    font-size: 12px;
    line-height: 14px;
  }
}
@media (min-width: 1200px) {
  .product button:hover,
  .product button:focus {
    color: #000000;
    background-color: transparent;
    text-decoration: none;
  }
  .product button:active {
    color: #fd0202;
    background-color: transparent;
    text-decoration: none;
  }
}
@media (max-width: 767px) {
  .product button {
    padding: 7px 0 0;
  }
}
.product button:active {
  color: #fd0202;
  background-color: transparent;
  text-decoration: none;
}
.product.remove-product {
  animation: scale 0.3s ease;
}
@keyframes scale {
  0% {
    transform: scale(0.9);
  }
  10% {
    transform: scale(0.8);
  }
  20% {
    transform: scale(0.7);
  }
  30% {
    transform: scale(0.6);
  }
  40% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(0.4);
  }
  60% {
    transform: scale(0.3);
  }
  75% {
    transform: scale(0.2);
  }
  85% {
    transform: scale(0.1);
  }
  100% {
    transform: scale(0);
  }
}
.quantity-select {
  display: flex;
  width: 114px;
  height: 40px;
  border: 1px solid #000000;
  position: relative;
}
@media (max-width: 767px) {
  .quantity-select {
    width: 80px;
    height: 34px;
  }
}
.quantity-select button {
  width: 40px;
  height: 100%;
  position: relative;
}
@media (max-width: 767px) {
  .quantity-select button {
    width: 25px;
    padding: 0;
  }
}
.quantity-select button::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 2px;
  background-color: #000000;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 1200px) {
  .quantity-select button:hover,
  .quantity-select button:focus {
    background-color: #f68050;
  }
  .quantity-select button:active {
    background-color: #d5450b;
  }
}
.quantity-select button:active {
  background-color: #d5450b;
}
.quantity-select__plus::before {
  content: "";
  position: absolute;
  width: 12px;
  height: 2px;
  background-color: #000000;
  left: 50%;
  transform: translateX(-50%) rotate(90deg);
}
.quantity-select button:disabled {
  cursor: default;
  background-color: transparent;
}
.quantity-select button:disabled:hover,
.quantity-select button:disabled:focus {
  background-color: transparent;
}
.quantity-select button:disabled::after,
.quantity-select button:disabled::before {
  background-color: #9f9f9f;
}
.quantity-select input[type="number"]::-webkit-outer-spin-button,
.quantity-select input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.quantity-select input[type="number"],
.quantity-select input[type="number"]:hover,
.quantity-select input[type="number"]:focus {
  appearance: none;
  -moz-appearance: textfield;
}
.quantity-select input {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  width: 32px;
  padding: 2px;
  border: none;
  text-align: center;
}
@media (max-width: 767px) {
  .quantity-select input {
    font-size: 16px;
    line-height: 18px;
  }
}
.quantity-select__error {
  display: block;
  font-size: 14px;
  width: 196px;
  height: auto;
  padding: 9px;
  margin-bottom: 10px;
  position: absolute;
  bottom: 100%;
  left: 30px;
  background-color: #fd0202;
  border-radius: 8px;
  z-index: 5;
}
@media (max-width: 767px) {
  .quantity-select__error {
    left: 12px;
  }
}
.quantity-select__error::after {
  content: "";
  height: auto;
  width: auto;
  position: absolute;
  left: 20px;
  top: 100%;
  border-color: #fd0202 transparent transparent;
  border-style: solid;
  border-width: 5px 7px 0;
}
.quantity-select__error p {
  color: #ffffff;
  margin: 0;
}
@media (max-width: 767px) {
  .quantity-select__error p {
    display: block;
  }
}
.quantity-select.is-error button::after {
  background-color: #fd0202;
}
.quantity-select.is-error .quantity-select__plus::before {
  background-color: #fd0202;
}
.quantity-select.is-error input {
  color: #fd0202;
}
.form-select {
  width: 100%;
  position: relative;
}
.form-select__group {
  margin: 0;
}
.form-select label {
  width: 100%;
  display: block;
  cursor: pointer;
}
.form-select button,
.form-select label {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: left;
  padding: 10px 50px 10px 0;
  color: #9f9f9f;
  margin-top: -9px;
  margin-bottom: 12px;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid #9f9f9f;
  transition: color 0.3s ease, background 0.4s ease, padding 0.4s ease;
}
@media (max-width: 767px) {
  .form-select button,
  .form-select label {
    padding: 6px 35px 6px 0;
    margin: 0;
    margin-top: 5px;
    margin-bottom: 2px;
  }
}
.form-select button::after,
.form-select label::after {
  content: "";
  position: absolute;
  background-image: url("../img/decorative/select-triangle.svg");
  width: 16px;
  height: 9px;
  right: 4px;
  top: 58%;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.form-select__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  line-height: 19px;
  color: #9f9f9f;
  background-color: #ffffff;
  box-shadow: 5px 5px 15px currentColor;
  position: absolute;
  top: 45px;
  left: 0;
  width: 100%;
  border-radius: 0 0 5px 5px;
  z-index: 5;
  transition: max-height 0.3s ease-in-out;
  max-height: 0;
  overflow: hidden;
}
.form-select__item button {
  width: 100%;
  padding: 6px;
  border-bottom: none;
  margin: 0;
  cursor: pointer;
  transition: color 0.3s ease, background 0.3s ease, padding 0.4s linear;
}
.form-select__item button::after {
  display: none;
}
@media (max-width: 767px) {
  .form-select__item button {
    padding: 6px 25px 6px 10px;
  }
}
@media (min-width: 1200px) {
  .form-select__item button:hover,
  .form-select__item button:focus {
    background-color: #f68050;
    color: #000000;
  }
  .form-select__item button:active {
    background-color: #d5450b;
    color: #9f9f9f;
  }
}
.form-select__item button:active {
  background-color: #f68050;
  transition-duration: 0s;
}
/* При добавлении класса */
.form-select.is-show button::after,
.form-select.is-show label::after {
  transform: rotate(180deg) translateY(50%);
}
@media (min-width: 1200px) {
  .form-select label:hover,
  .form-select label:focus {
    border-radius: 10px;
    color: #000000;
    padding: 10px 50px 10px 10px;
    background: linear-gradient(90deg, #0c0c0c 28%, #f68050 70%);
  }
  .form-select label:active {
    background-color: transparent;
    color: #9f9f9f;
  }
}
.form-select label:active {
  background-color: transparent;
  color: #9f9f9f;
}
@media (min-width: 1200px) {
  .form-select button:hover,
  .form-select button:focus {
    color: #f68050;
    background: linear-gradient(90deg, #0c0c0c 28%, #f68050 70%);
    padding: 6px 6px 6px 15px;
  }
  .form-select button:active {
    background-color: transparent;
    color: #9f9f9f;
  }
}
.form-select button:active {
  background-color: transparent;
  color: #9f9f9f;
}
